import { TImage } from "@/types";
import { StrapiImage } from "../../ui/strapi-image";


export interface IGalleryProps {
    id: number;
    documentId: string;
    __component: string;
    heading: string;
    subHeading: string;
    description: string;
    image: TImage[];
}

export function Gallery({ data }: { data: IGalleryProps }) {
    if (!data) return null;

    const { heading, subHeading, description, image } = data;
    return (
        <section className="px-8 pb-6 mx-auto md:px-6 lg:py-2 bg-brand-cream">
            <div className="container mx-auto max-w-6xl">
                <div className="mb-12 text-center">
                    <h2 className="mb-2 text-2xl text-brand-gold font-extrabold font-heading">{heading}</h2>
                    <p className="text-xl text-white">{subHeading}</p>
                    {description && (
                        <p className="mx-auto mt-4 max-w-2xl text-brand-dark-grey">{description}</p>
                    )}
                </div>
               <div className="rounded-lg border bg-black text-white border-gray-700 my-4 py-6 px-10 shadow-sm transition-shadow hover:shadow-md">
                    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                        {image.map((img: TImage) => (
                            <div key={img.id} className="overflow-hidden rounded-lg">
                                <StrapiImage
                                    src={img.url}
                                    alt={img.alternativeText || "Gallery Image"}
                                    className="w-full h-auto object-cover transform hover:scale-105 transition-transform duration-300"
                                    width={1080}
                                    height={1920}
                                />
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </section>
    )
}